$font-family: "Gilroy", sans-serif;
$color-brand: #ff4419;

.btn_naranja {
     background-color: $color-brand;
     color: white;
     font-family: $font-family;
     font-size: 18px;
     padding: 10px 0;
     min-width: 200px;
     @media (min-width: 320px) and (max-width: 991px) {
          font-size: 20px;
          width: 100%;
     }
     @media (max-width: 992px) and (max-width: 1199px) {
          font-size: 22px;
     }
     &:hover {
          background-color: $color-brand;
          color: white;
     }
}

.btn_blanco{
     background-color: white;
     color: $color-brand;
     border-color: $color-brand;
     font-family: $font-family;
     font-size: 18px;
     padding: 10px 0;
     min-width: 200px;
     @media (min-width: 320px) and (max-width: 991px) {
          font-size: 20px;
          width: 100%;
     }
     @media (max-width: 992px) and (max-width: 1199px) {
          font-size: 22px;
     }
     &:hover {
          background-color: white;
          color: $color-brand;;
          border-color: white;
     }
}

.btn-planes {
     border: 1px solid $color-brand;
     background-color: white;
     border-radius: 6px;
     color: $color-brand;
     font-size: 16px;
     width: 37%;
     font-weight: 500;
     @media (max-width: 991px) {
          width: 100%;
     }
     &:hover {
          border: 1px solid $color-brand;
          color: white;
          background-color: $color-brand;
     }
}

.btn_whatsapp {
     background: linear-gradient(180deg, #60d66a 42%, #20b038 100%);
     color: white;
     padding: 10px 20px;
     border-radius: 6px;
}

